.top-icon {
    width: auto !important;
    margin: auto !important;
}

.zstack {
    display: grid;
    align-items: center;
    justify-items: center;
}

.zstack>* {
    grid-area: 1/1/1/1;
}


li > a,
.nav-link-active,
.link {
    color: #2C6030 !important;
}

.vr {
    color: #2C6030 !important;
    width: 1.60px;
}

.opac {
    opacity: 1 !important;
}

.zic {
    color: #2C6030 !important;
    /* opacity: 0.3; */
    margin-bottom: -1px;
    margin-top: -1px;
}

.ml-100 {
    margin-left: 150px;
    width: 100px;
    text-align: left;
}

.mb-50 {
    margin-bottom: 50px;
}

.version-number {
    font-size: 15px;
    opacity: 0.5;
    font-weight: bolder;
}


@media only screen and (min-width: 768px) {
    .top-icon {
        width: 700px !important;
        margin: auto !important;
    }
}
